body {
	background-image: radial-gradient(#c9c9c9 1.5px, #e0e0e9 1px);
	background-size: 20px 20px;
	font-family: "Ubuntu", sans-serif;
}

.app-body {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.logo {
	margin: 30px auto;
	font-size: 40px;
	font-weight: 700;
	padding: 15px;
	background-color: white;
	border-radius: 20px;
	border-bottom: 4px solid #f34e4b;
}

.logo span {
	background-color: #f34d4b;
	padding: 3px 7px;
	border-radius: 10px;
	margin-right: 5px;
	color: white;
}

.search-area {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.search-area form {
	width: 50vw;
	background-color: #f4f4fb;
	border-radius: 20px;
	margin: 0 10px 10px 10px;
	padding: 20px;
	border-bottom: 4px solid #f34e4b;
}

.fa-icon {
	margin-right: 5px;
}

.fa-icon > path {
	color: #f34e4b;
}

form label p,
.watch-area .description span {
	font-size: 1.1rem;
	background-color: #e0e0e9;
	width: fit-content;
	padding: 3px 5px;
	border-radius: 10px;
}

.search-area input[type="text"],
.search-area select {
	width: 100%;
	margin: 6px 0 20px 0;
	font-size: 1.1rem;
	padding: 5px;
	border-radius: 10px;
	border: none;
	border-bottom: 2px solid transparent;
}

.search-area input[type="text"]:focus,
.search-area select:focus {
	outline: none;
	border-bottom: 2px solid #f34e4b;
}

#advanced {
	display: none;
}

#advanced + label {
	display: flex;
	align-items: center;
	margin-bottom: 20px;
}

.custom-checkbox {
	width: 25px;
	height: 25px;
	background-color: white;
	margin-right: 5px;
	border-radius: 50%;
	border: 2px solid #f34d4b;
}

#advanced:checked + label .custom-checkbox {
	position: relative;
	background-color: #f34d4b;
}
#advanced:checked + label .custom-checkbox::after {
	content: "\2713";
	position: absolute;
	top: -1.2px;
	color: white;
	left: 4px;
	font-weight: 700;
}

.search-area button {
	margin: 0 auto;
	display: block;
	padding: 5px 25px;
	color: white;
	font-size: 1.1rem;
	background-color: #f34d4b;
	border: none;
	border-radius: 10px;
	cursor: pointer;
}

.search-result {
	background-color: #f4f4fb;
	border-radius: 20px;
	max-width: 1200px;
	margin: 20px 10px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	padding-top: 20px;
}

/* single video result div - edit this for transition */
.video-snippet-container {
	background-color: white;
	width: 100%;
	display: flex;
	padding: 25px;
	text-decoration: none;
	border-radius: 15px;
	margin: 0 20px 20px 20px;
	border-bottom: 4px solid transparent;
}

.video-snippet-container:hover {
	border-bottom: 4px solid #f34e4b;
}

.video-snippet-info {
	margin-left: 20px;
}

.video-snippet-info > * {
	margin: 15px 0;
}

.video-snippet-title,
.watch-area .title {
	margin: 0px;
	padding: 10px;
	background-color: #f34e4b;
	border-radius: 10px;
	display: flex;
	color: white;
}
.video-snippet-title .fa-icon > path,
.video-snippet-title h3,
.watch-area .title .fa-icon > path {
	color: white;
}
.video-snippet-title .fa-icon,
.watch-area .title .fa-icon {
	font-size: 23px;
}
.video-snippet-image img {
	border-radius: 10px;
}

.video-snippet-stats {
	display: flex;
	justify-content: space-between;
	text-align: center;
}

.watch-area > *:not(.player, .description) {
	margin: 10px 0;
}

.watch-area {
	background-color: white;
	max-width: 879px;
	width: 95vw;
	padding: 15px;
	border-radius: 20px;
	margin-bottom: 20px;
	border-bottom: 4px solid #f34e4b;
}

.watch-area .player {
	text-align: center;
	position: relative;
	padding-bottom: 56.25%;
	height: 0;
}

.watch-area iframe {
	border-radius: 15px;
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
}

.watch-area .description {
	white-space: pre-wrap;
	word-break: break-word;
	background-color: #f4f4fb;
	padding: 0 10px 10px 10px;
	border-radius: 10px;
	font-size: 15px;
}

.watch-area .description span {
	line-height: 2.5;
}
.watch-area .description p {
	line-height: 1.4;
}
.watch-area .description p a:link,
:visited {
	color: #f34e4b;
}

.watch-area .video-stats {
	display: flex;
	flex-wrap: wrap;
}

.watch-area .video-stats > *:first-child {
	margin-right: auto;
	margin-left: 13px;
}

.watch-area .video-stats > *:nth-child(2) {
	display: flex;
	justify-content: space-around;
}

.watch-area .video-stats > *:nth-child(2) > * {
	margin-left: 20px;
}

.error-area {
	background-color: #e42e2e;
	height: 100%;
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	display: flex;
	justify-content: center;
	align-items: center;
}

.error-box {
	background-color: #f4f4fb;
	height: fit-content;
	border-radius: 20px;
	padding: 10px;
	box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
	margin: 10px;
}

.error-info {
	padding: 10px;
	border-radius: 15px;
	background: url(error-icon.svg) #e42e2e;
	background-repeat: no-repeat;
	background-position: 95%;
	background-size: 45px;
}
.error-info h1,
.error-info h6 {
	color: #f4f4fb;
	margin: 3px 0;
}

.error-text {
	background-color: #eaecf4;
	border-radius: 15px;
	padding: 1px 5px 10px 5px;
	margin: 10px 0;
}

.error-text h3 {
	margin: 10px 0;
}

.error-text dd {
	margin-left: 10px;
}

.error-box h4 {
	background-color: #008fff;
	padding: inherit;
	border-radius: 15px;
	text-align: center;
}
.error-box a {
	color: #f4f4fb;
}

.error-redirect {
	background-color: #82b646;
	color: #f4f4fb;
	border-radius: 15px;
	padding: 10px;
	margin-bottom: 10px;
	text-align: center;
}

@media screen and (max-width: 768px) {
	.logo {
		margin: 20px auto;
		font-size: 30px;
	}
	.search-area form {
		width: 80vw;
		margin: 0;
	}
	.search-result {
		justify-content: space-around;
		padding: 0px;
		padding-right: 10px;
	}
	.video-snippet-container {
		width: 47.9%;
		flex-direction: column;
		margin: 10px 0 10px 10px;
		padding: 10px;
		border-bottom: 4px solid #f34e4b;
	}
	.video-snippet-image img {
		width: 100%;
	}
	.video-snippet-info {
		margin: 0;
	}
	.video-snippet-info > *:first-child {
		margin-top: 5px;
	}

	.watch-area .video-stats > *:nth-child(2) > h5 {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin: 0 15px;
	}
	.watch-area .video-stats > *:nth-child(2) > h5 .fa-icon {
		margin-bottom: 5px;
	}
}

@media screen and (max-width: 530px) {
	.video-snippet-container {
		width: 100%;
		margin-bottom: 10px;
	}
	.watch-area .video-stats > *:nth-child(2) {
		margin: 15px 0 10px 0;
		width: 100%;
		justify-content: space-evenly;
	}
}

.dots div {
	height: 50px;
	width: 5px;
	background-color: #f34d4b;
	border-radius: 50%;
	position: absolute;
	animation: rotate360 1.2s ease-in-out infinite;
	animation-delay: calc(70ms * var(--d));
}

@keyframes rotate360 {
	0% {
		opacity: 0.5;
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: rotate(360deg);
	}
}
